@font-face {
    font-family: "Roboto Regular";
    src: url(../Assets/Roboto/Roboto-Regular.ttf) format("truetype");
    font-weight: normal;
}
@font-face {
    font-family: "Roboto Extra Bold";
    src: url(../Assets/Roboto/Roboto-Bold.ttf) format("truetype");
    font-weight: normal;
}
@font-face {
    font-family: "Roboto Light Italic";
    src: url(../Assets/Roboto/Roboto-Italic.ttf) format("truetype");
    font-weight: normal;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.footer-sec{
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
    padding: 50px;
    width: 100%;
}
.footer-sec h1{
    align-self: center;
    justify-self: center;
    text-align: center;
    padding: 40px;
    font-family: "Roboto Italic";
    font-size: 3rem;
    height: 100px;
    font-weight: lighter;
}
.footer-sec-icons{
    display: flex;
    align-self: center;
    justify-self: center;
    justify-content: space-around;
    width: 50%;
    padding-top: 10px;
}
hr{
    width: 50%;
    align-self: center;
}
.footer-sec img{
    width: auto;
    height: 30px;
}
@media screen and (max-width:600px){
    .footer-sec h1{
        width: max-content;
    }
    hr{
        width: 90%;
    }
    .footer-sec-icons{
        display: flex;
        gap: 15px;
        justify-content: center;
    }
}